.section-2 {
    position: relative;
    background: url(../img/section-2-bg.jpg) no-repeat center center/cover;
    padding-bottom: 139px;
    .container {
        background: rgba(255,255,255, 0.81);
        border-radius: 10px;
        padding: 33px 285px 22px 36px;
        position: relative;
        transform: translate(0px, -20px);
        &:after {
            content: url('../img/section-2-cart.png');
            display: block;
            position: absolute;
            top: -177px;
            right: 20px;
            z-index: 9;
        }
        
        p {
            font-size: 23px;
            line-height: 29px;
            font-family: 'Cera Pro Medium';
            margin-top: 23px;
            .red {
                color: #CC1960;
            }
            .bold {
                font-family: 'Cera Pro Bold';
            }
        }
    }

    @media screen and (max-width: 1300px) {
        padding-bottom: 100px;
        .container {
            transform: translate(0px, -50px);
            padding: 30px 30px 22px 30px;
            
            &:after {
                top: -216px;
            }
        }
    }
    @media screen and (max-width: 1100px) {
        .container {
            padding: 25px 25px 22px 25px;
            &:after {
                right: 0;
            }
            
            p {
                font-size: 20px;
                line-height: 25px;
            }
        }
    }
    @media screen and (max-width: 991px) {
        padding-bottom: 50px;
        .container {
            
            transform: translate(0px, -60px);
            &:after {
                zoom: 90%;
            }
            
            p {
                font-size: 19px;
                line-height: 23px;
            }
        }
    }
    @media screen and (max-width: 768px) {
        padding-bottom: 0px;
        .container {
            padding: 18px 24px 20px 20px;
            transform: translate(0px, -21px);
            margin-top: -65px;
            &:after {
                zoom: 56%;
            }
            p {
                margin-top: 15px;
                font-size: 17px;
                line-height: 21px;
            }
        }
    }
    @media screen and (max-width: 640px) {
        .container {
            &:after {
                zoom: 50%;
                top: -246px;
            }
            p {
                font-size: 15px;
                line-height: 19px;
            }
        }
    }
    @media screen and (max-width: 575px) {
        padding-bottom: 104px;
    }
    @media screen and (max-width: 500px) {
        .container {
            &:after {
                zoom: 45%;
            }
            p {
                font-size: 13px;
                line-height: 20px; 
            }
        }
    }
    @media screen and (max-width: 400px) {
        .container {
            
            h2 {
                font-size: 15px;
            }
            p {
                font-size: 12px;
                line-height: 19px;
                margin-top: 12px; 
            }
        }
    }
    @media screen and (max-width: 360px) {
        .container {
            padding: 18px 12px 21px 10px;
            transform: translate(0,-12px);
            max-width: 91vw;
            h2 {
                font-size: 13px;
                line-height: 16px;
            }
            p {
                font-size: 11px;
                line-height: 14px;
            }
        }
    }

    
}