.section-4 {
    position: relative;
    margin-top: -30px;
    background: linear-gradient(133.18deg, #FCF7F0 40.59%, #FCF7F0 74.88%);
    z-index: 999;
    &:after {
        content:url(../img/car2.png);
        display: block;
        position: absolute;
        z-index: 99;
        top: -290px;
        left: calc(50% - 583px);
        z-index: 0;
        pointer-events: none;
    }
    .container {
        background: #fff;
        position: relative;
        
        z-index: 1;
        padding: 39px 38px 34px;
        
        p {
            margin-top: 15px;
            font-size: 22px;
            line-height: 22px;
            
            span {
                font-family: 'Cera Pro Medium';
            }
        }
    }
    .document{
        margin-top: 13px;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        &-item {
            margin-top: 37px;
            width: 350px;
            height: 210px;
            position: relative;
            cursor: pointer;
            img {
                width: 100%;
                height: 100%;
                object-fit:  cover;
                border-radius: 20px;
            }
            div {
                position: absolute;
                top: 0;
                font-family: 'Cera Pro Bold';
                width: 100%;
                height: 47px;
                border-radius: 20px 20px 0 0;

                display: flex;
                justify-content: center;
                align-items: center;

                background: rgba(0,0,0, 0.56);
                font-size: 22px;
                line-height: 22px;
                text-align: center;

                color: #FFFFFF;
            }
        }
        
        .document-hamburger {
            display: none;
        }
        &.open {
            position: fixed;
            top: 0;
            left: 0;
            width: 100vw;
            height: 100vh;
            background: rgba(0,0,0, 0.8);
            display: flex;
            justify-content: center;
            align-items: center;
            margin-top: 0;
            z-index: 999999;
            .document-item {
                display: none;
                
                &.active {
                    display: block;
                    width: auto;
                    height: auto;
                    margin-top: 0;
                    position: relative;
                    cursor: auto;
                    img {
                        width: auto;
                        height: auto;
                        max-width: 90vw;
                        max-height: 90vh;
                        border-radius: 0;
                        display: block;
                        margin: 0 auto;
                        transform: translateY(5vh);
                    }
                    .document-title {
                        border-radius: 0;
                        background: none;
                        position: absolute;
                        transform: translateY(-5vh);
                        z-index: 2;
                        width: 100%;
                        height: auto;
                        display: flex;
                        align-items: center;
                        flex-wrap: nowrap;
                        justify-content: space-between;
                        text-align: start;
                        height: 10vh !important;
                        p {
                            font-size: 38px;
                            line-height: 44px;
                            margin-top: 0;
                            display: block;
                            padding-right: 20px;
                        } 
                    }
                    .document-hamburger {
                        cursor: pointer;
                        display: flex;
                        flex-direction: column;
                        transition: all 1s ease;
                        position: relative;
                        
                        
                        z-index: 99;
                        width: 30px;
                        height: 30px;
                        margin-bottom: 0;
                        transform: translate(-4px, 0vh);
                        zoom: 160%;
                        background: none;
                        span {
                            width: 30px;
                            height: 4px;
                            background: #fff;
                            display: inline-block;
                            margin-top: 3px;
                            transition: all 1s ease;
                            margin-top: 0;
                            position: absolute;
                            
                            &:nth-child(1) {
                                transform:  rotate(45deg);
                            }
                            &:nth-child(2) {
                                transform:  rotate(-45deg);
                            }
                            &:nth-child(3) {
                                display: none;
                            }
                            
                            &:first-child {
                                margin-top: 0;
                            }
                        }
                    }
                }
            }
        }
    }
    @media screen and (max-height: 900px) and (orientation: landscape) {
        .document.open .document-item.active .document-title {
            
        }
        .document.open .document-item.active .document-title p {
            font-size: 35px;
            line-height: 40px;

        }
    }
    @media screen and (max-height: 832px) {
        .document.open .document-item.active .document-title {
            transform: translateY(-4vh);
            height: 7vh;
        }
        .document.open .document-item.active .document-title p {
            font-size: 28px;
            line-height: 28px;
        }
    }
    @media screen and (max-height: 750px) {
        .document.open .document-item.active .document-title {
           
            height: 6vh;
        }
        .document.open .document-item.active .document-title p {
            font-size: 24px;
            line-height: 24px;
        }
    }
