.header {
    
    color: #fff;
    padding: 269px 0 168px 0;
    position: relative;
    &:after {
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        background: url(../img/main-bg.jpg) no-repeat center center/cover;
        top: 0;
        left: 0;
        z-index: -1;
        filter: brightness(0.7);
    }
    .container {
        box-shadow: none;
        max-width: 100vw;
        margin: 0 auto;
        
    }
    
    .header-title {
        max-width: 700px;
        z-index: 99;
        h1 {
            font-family: 'Gilroy Bold';
            font-size: 39px;
            line-height: 47px;
        }
        
        &__desc {
            font-family: 'Gilroy Medium';
            font-size: 27px;
            line-height: 32px;
            margin-top: 40px;
        }
        &__btn {
            color: #fff;
            font-family: 'Cera Pro Bold';
            font-size: 20px;
            line-height: 25px;
            margin-top: 50px;
            background: rgb(94,48,157);
            background: linear-gradient(135deg, rgba(94,48,157,1) 0%, rgba(255,133,118,1) 100%);
            width: 236px;
            height: 57px;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 5px;
            box-shadow: 2px 6px 17px rgba(219, 114, 127, 0.32);
            cursor: pointer;
        }
    }
    


    @media screen and (max-width: 1300px) {
        .container {
            
            .header-title {
                
                h1 {
                    font-size: 35px;
                    line-height: 40px;
                }
                
                &__desc {
                    font-size: 26px;
                    line-height: 30px;
                }
                &__btn {
                    font-size: 18px;
                    width: 210px;
                    height: 50px;
                }
            }
        }
    }
    @media screen and (max-width: 1100px) {
        padding: 219px 0 118px 0;
        .container {
            
            .header-title {
                max-width: none;
            }
        }
    }
    @media screen and (max-width: 991px) {
        .container {
            
            .header-title {
                
                h1 {
                    font-size: 33px;
                    line-height: 38px;
                }
                
                &__desc {
                    margin-top: 40px;
                    font-size: 25px;
                    line-height: 28px;
                }
                &__btn {
                    margin-top: 50px;
                    font-size: 17px;
                    width: 210px;
                    height: 50px;
                }
            }
        }
    }
    @media screen and (max-width: 860px) {
        .header-title {
            padding-left: 38px;
        }
    }
    @media screen and (max-width: 768px) {
        padding-top: 148px;
        padding-bottom: 132px;
        .container {
            
            .header-title {
                padding-left: 0;
                h1 {
                   font-size: 29px;
                   line-height: 35px;
                }
                &__desc {
                    margin-top: 36px;
                   font-size: 24px;
                   line-height: 28px;
                }
                &__btn {
                    margin-top: 41px;
                    font-size: 17px;
                    line-height: 21px;
                    height: 42px;
                    width: 197px;
                }
            }
        }
    }
    @media screen and (max-width: 640px) {
        
        .container {
            
            .header-title {
              
                h1 {
                   font-size: 25px;
                   line-height: 30px;
                }
                &__desc {
                    margin-top: 30px;
                   font-size: 20px;
                   line-height: 24px;
                }
                &__btn {
                    margin-top: 35px;
                    font-size: 16px;
                    line-height: 20px;
                    height: 38px;
                    width: 187px;
                }
            }
        }
    }
    @media screen and (max-width: 575px) {
        padding-top: 130px;

        .container {
            
            .header-title {
              
                h1 {
                   font-size: 22px;
                   line-height: 28px;
                }
                &__desc {
                    margin-top: 25px;
                   font-size: 17px;
                   line-height: 22px;
                }
                &__btn {
                    margin-top: 30px;
                    font-size: 15px;
                    line-height: 18px;
                    height: 35px;
                    width: 177px;
                }
            }
        }
    }
    @media screen and (max-width: 451px) {
        .container {
            
            .header-title {
              
                h1 {
                   font-size: 20px;
                   line-height: 25px;
                }
                &__desc {
                   font-size: 15px;
                   line-height: 20px;
                }
                &__btn {
                    font-size: 14px;
                    line-height: 17px;

                    height: 35px;
                    width: 157px;
                }
            }
        }
    }
    @media screen and (max-width: 365px) {
        padding-top: 124px;
        .container {
            
            .header-title {
                h1 {
                    font-size: 17px;
                    line-height: 21px;
                 }
                 &__desc {
                     margin-top: 15px;
                    font-size: 14px;
                    line-height: 16px;
                 }
                 &__btn {
                     margin-top: 30px;
                     font-size: 12px;
                     line-height: 15px;
                     height: 34px;
                     width: 144px;
                 }
            }
           
        }
    }
}