.section-3 {
    background: rgb(252,247,240);
    background: linear-gradient(180deg, rgba(252,247,240,0) 0%, rgba(252,247,240,1) 100%);
    position: relative;
    .container {
        padding: 41px 45px 62px;
        background: #fff;
        transform: translate(0px, -136px);
        border-radius: 10px 10px 0px 0px;
    }
    .icons {
        margin-top: 75px;
        display: flex;
        justify-content: space-between;
        &__item {
            display: flex;
            flex-direction: column;
            width: auto;
            text-align: center;
            align-items: center;
            
            &-img {
                width: 134.2px;
                height: 134.2px;
                background: #FFFFFF;
                box-shadow: 0px 12px 30px rgba(255, 0, 122, 0.07);
                border-radius: 50%;
                display: flex;
                justify-content: center;
                align-items: center;
                position: relative;
                div {
                    position: absolute;
                    top: 0px;
                    right: 0px;
                    z-index: 2;
                    font-size: 70px;
                    line-height: 121.2%;
                    font-family: 'CedarvilleCursive-Regular';
                    color: #E41C6B;
                    transform: translate(-19px,-39px);
                }
            }
            &:nth-child(2), &:nth-child(3) {
                .icons__item-img {
                    div {
                        transform: translate(0px,-39px);
                    }
                }
                
                
            }
            h3 {
                text-align: center;
                font-size: 20px;
                line-height: 121.2%;
                height: 65px;
                font-family: 'Cera Pro Bold';
                width: 100%;
                margin-top: 25px;
            }
            &-desc {
                margin-top: 27px;
                font-size: 15px;
                line-height: 121.2%;
                font-family: 'Cera Pro Regular';
                color: #505050;
            }
        }
    }
    &-bottom.container {
        margin-top: -5px;
        box-shadow: 0px -5px 18px rgba(0,0,0, 0.04);
        font-size: 20px;
        line-height: 25px;
        padding: 28px 43px 53px;
        border-radius: 10px 10px 10px 10px;
        p {
            margin-top: 8px;
            padding-right: 155px;
            
            &:last-child {
                margin-top: 5px;
            }
            &.medium {
                font-family: 'Cera Pro Medium';
                font-size: 19px;
            }
            &:first-child {
                margin-top: 0;
            }
            .purple {
                color: #5800C9;
                font-family: 'Cera Pro Medium';
            }
            .red {
                color: #CC1960;
                font-size: 26px;
                line-height: 33px;
                font-family: 'Cera Pro Bold';
                position: relative;
                right: -18px;
            }
            .red2 {
                font-size: 22px;
                line-height: 28px;
            }
        }


        
    }

    @media screen and (max-width: 1300px) {
        .container {
            transform: translate(0px,-100px);
        }
        &-bottom.container {
            p {
                padding-right: 0;
            }
        }
    }
    @media screen and (max-width: 1100px) {
        &-bottom.container {
            font-size: 18px;
            line-height: 23px;
            p {
                &.medium {
                    font-size: 17px;
                    line-height: 22px;   
                }
                .red {
                   
                    font-size: 20px;
                    line-height: 27px;
                    
                }
                .red2 {
                    font-size: 18px;
                    line-height: 24px;
                }
            }
        }
    }
    @media screen and (max-width: 991px) {
        // transform: translate(-27px,-100px);
        .container {
            transform: translate(0px,-60px);
        }
    }
    @media screen and (max-width: 768px) {
        .container {
            transform: none;
            margin-top: 60px;
            padding: 18px 40px 35px 18px;
            .icons {
                flex-wrap: wrap;
                display: flex;
                margin-top: 0;
                transform: translateY(-27px);
                &__item {
                    margin-top: 86px;
                    width: 50%;
                }
            }
        }
        .section-3-bottom {
            margin-top: -5px;
            
        }

        &-bottom.container {
            font-size: 15px;
            line-height: 19px;
            padding: 17px 20px 24px 15px;
            p {
                &.medium {
                    font-size: 14px;
                    line-height: 18px;   
                }
                .red {
                   
                    font-size: 16px;
                    line-height: 20px;
                    
                }
                .red2 {
                    font-size: 15px;
                    line-height: 19px;
                }
            }
        }
    }
    @media screen and (max-width: 640px) {
        &-bottom.container {
            p {
                &.medium {
                   padding-right: 115px;  
                }
            }
        }
    }
    @media screen and (max-width: 575px) {
        margin-top: -114px;
        .container {
            h2 {
                margin-bottom: 10px;
            }
            .icons {
                transform: 0;
                &__item {
                    margin-top: 68px;
                    width: 100%;
                    h3 {
                        margin-top: 19px;
                        height: auto;
                    }
                    &-desc {
                        margin-top: 16px;
                    }
                }
            }
        }
        &-bottom.container {
            p {
                &.medium {
                   padding-right: 59px;  
                }
                
            }
        }
    }
    @media screen and (max-width: 474px) {
        &-bottom.container {
            font-size: 14px;
            line-height: 18px;
            p {
                &.medium {
                    font-size: 13px;
                    line-height: 20px;
                    padding-right: 1px;
                }
                .red {
                   
                    font-size: 15px;
                    line-height: 18px;
                    
                }
                .red2 {
                    font-size: 14px;
                    line-height: 18px;
                }
            }
        }
    }
    @media screen and (max-width: 400px) {
        .container {
            padding: 18px 20px 35px;
            .icons {
                &__item {
                    zoom: 90%;
                }
            }
        }
    }
    @media screen and (max-width: 390px) {
        &-bottom.container {
            padding: 10px 10px 19px 9px;
            font-size: 14px;
            line-height: 18px;
            box-shadow: 0px -2px 5px rgba(0, 0, 0, 0.14);
            p {
                &.medium {
                    font-size: 12px;
                    line-height: 21px;
                    padding-right: 0;
                }
                .red {
                    font-size: 14px;
                    line-height: 18px;
                    right: -6px;
                }
                .red2 {
                    font-size: 14px;
                    line-height: 18px;
                }
            }
        }
    }
    @media screen and (max-width: 360px) {
        .container {
            .icons {
                &__item {
                    zoom: 75%;
                }
            }
        }
    }

    
    
}