.section-6 {
    background: linear-gradient(182.97deg, #FCF1F9 6.39%, rgba(252, 247, 240, 0) 59.32%);
    padding-top: 80px;
    .container {
        background: #fff;
        padding: 48px 48px 75px;

    }
    .contact {
        
        margin-top: 34px;
        p {
            font-size: 23px;
        line-height: 29px;  
            padding-right: 23px;
            &:nth-child(2) {
                margin-top: 19px;
            }
        }
        .medium {
            font-family: 'Cera Pro Medium';
            line-height: 40px;
            
        }
        .purple {
            color: #5800C9;
        }
        .red {
            color: #CC1960;
            font-size: 26px;
            line-height: 33px;
            font-family: 'Cera Pro Bold';
            transform: translateX(15px);
            display: inline-block;
        }
        .red2 {
            font-size: 22px;
            line-height: 28px;
        }
    }

    @media screen and (max-width: 1100px) {
        .container {
            padding: 25px 20px 40px 20px;
            
        }
        .contact {
            p {
                font-size: 20px;
                line-height: 24px;
            }
            
         
            .medium {
               
                font-size: 17px;
                line-height: 35px;
            }
            .red {
                font-size: 23px;
                line-height: 30px;
                transform: translateX(10px);
            }
        }
    }
    @media screen and (max-width: 768px) {
        .container {
            padding: 25px 15px 34px 15px;
            
        }
        .contact {
            p {
                font-size: 16px;
                line-height: 20px;
                font-family: 'Cera Pro Medium';
                &:nth-child(2) {
                    margin-top: 25px;
                }
            }
            margin-top: 26px;
            .purple {
                color: #000;
            }
            .medium {
                font-family: 'Cera Pro Bold';
                font-size: 13px;
                line-height: 30px;
            }
            .red {
                font-size: 19px;
                line-height: 24px;
                transform: translateX(10px);
            }
        }
    }
    @media screen and (max-width: 640px) {
        .contact {
            p {
                &:nth-child(2) {
                }
            }
            .medium {
                margin-top: 5px;
              padding-right: 100px;
              line-height: 20px;
            }
            .red {
                font-size: 17px;
                line-height: 22px;
             
            }
        }
    }
    @media screen and (max-width: 575px) {
        .container {
            padding: 13px 10px 14px 15px;
        }
        padding-top: 60px;
        padding-bottom: 60px !important;
        .contact {
            margin-top: 16px;
            p {
                padding-right: 0;
                font-size: 13px;
                line-height: 16px;
                font-family: 'Cera Pro Regular';
                &:nth-child(2) {
                    margin-top: 18px;
                }
                &:last-child {
                    display: none;
                }
            }
            .purple-last {
                font-family: 'Cera Pro Medium';
                color: #5800C9;
            }
            .medium {
                margin-top: 0px;
              padding-right: 0px;
              line-height: 17px;
            }
            .red {
                font-size: 15px;
                line-height: 19px;
             
            }
        }
    }
}