
// fonts
@import "fonts";



* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    outline: none;
    &:focus {
        outline: none;
    }
    // border: 1px solid red;
    
    // &::-webkit-scrollbar {width: 0px;
    //     height: 0;
    // }
    
}

h1,h2,h3,h4,h5,h6 {
    margin: 0;
    padding: 0;
}

button {
    border: none;
    background: none;
    outline: none;
    &:focus {
        outline: none;
    }
}

a {
    text-decoration: none;
    text-transform: none;
    outline: none;
  
    &:focus {
        text-decoration: none;
        text-transform: none;
        outline: none;
    }
    &:active {
        text-decoration: none;
        text-transform: none;
        outline: none;
    }
    &:hover {
        text-decoration: none;
        text-transform: none;
        outline: none;
      
    }
}
body {
    font-size: 20px;
    line-height: 25px;
    font-family: "Cera Pro Regular";
    background: #FDFBF8;
    max-width: 100vw;
}

.container {
    width: 1200px;
    margin: 0 auto;
    border-radius: 10px 10px 10px 10px;
    box-shadow: 1px 2px 27px rgba(0,0,0, 0.12);
    @media screen and (max-width: 1300px) {
        width: 992px;
    }
    @media screen and (max-width: 1100px) {
        width: 850px;
    }
    @media screen and (max-width: 991px) {
        width: 768px;
    }
    @media screen and (max-width: 768px) {
        width: 593px;
    }
    @media screen and (max-width: 640px) {
        width: 480px;
    }
    @media screen and (max-width: 575px) {
        width: 85vw;
    }
    @media screen and (max-width: 400px) {
        width: 90vw;
    }
    @media screen and (max-width: 365px) {
        width: 94vw;
    }
}
h2 {
    font-size: 31px;
    line-height: 39px;
    font-family: 'Cera Pro Bold';
}
@media screen and (max-width: 1100px) {
    h2 {
        font-size: 27px;
        line-height: 35px;
    }
}
@media screen and (max-width: 991px) {
    h2 {
        font-size: 25px;
    }
}
@media screen and (max-width: 768px) {
    h2 {
        font-size: 23px;
        line-height: 29px;
    }
}
@media screen and (max-width: 640px) {
    h2 {
        font-size: 20px;
        line-height: 25px;
    }
}
@media screen and (max-width: 500px) {
    h2 {
        font-size: 17px;
        line-height: 22px;
    }
}
@media screen and (max-width: 380px) {
    h2 {
        font-size: 16px;
        line-height: 20px;
    }
}
