
.section-6 {
    padding-bottom: 96px;
}
.footer {
    position: relative;
    box-shadow: 0 -3px 5px rgba(0,0,0, 0.3);
    padding: 41px 0 39px 0;
    background: #fff;
    .logo {
        zoom: 112%;
    }
    .navbar {
        position: relative;
        .container {
            box-shadow: none;
            height: auto;
        }
    }
    .gmail-icon {
        display: none;

    }
    .red {
        a {
            font-family: 'Cera Pro Bold';
            font-size: 26px;
            line-height: 33px;
            color: #CC1960;
        }
    }
    .footer-block {
        display: flex;
        flex-direction: column;
        flex-grow: 2;
        .menu {
            justify-content: space-between;
            li {
                margin-right: 0;
            }
        }
        margin-left: 97px;
    }
    .footer_contact {
        margin-top: 39px;
        display: flex;
        .phone {
            margin-left: 47px;
        }
        .icons {
            margin-left: 15px;
        }
    }
    @media screen and (max-width: 1300px){
        .footer-block {
            margin-left: 60px;
            .menu {
              
                li {
                    a {
                        font-size: 15px;
                    }
                }
            }
         
        }
        .logo {
            zoom: 105%;
        }
    }
    @media screen and (max-width: 1100px){
        padding: 31px 0 29px;
        .footer-block {
            margin-left: 0px;
        }
        .logo {
            zoom: 90%;
            width: auto;
            margin-right: 50px;
        }
        .red {
            a {
                font-family: 'Cera Pro Bold';
                font-size: 22px;
                line-height: 30px;
                color: #CC1960;
            }
        }
        .container {
            flex-wrap: nowrap;
        }
        .footer-block {
            nav {
                order: 0;
                margin: 0;
                .menu {
                    li {
                        a {
                            font-size: 14px;
                        }
                    }
                }
            }
        }
        .footer_contact {
            margin-top: 20px;
            .red {
                a {
                    font-size: 20px;
                }
            }
            .phone {
                margin-left: 27px;
            }
            .icons {
                margin-left: 15px;
                a {
                    zoom: 90%;
                }
            }
        }
    }
    @media screen and (max-width: 991px){
        .footer-block nav .menu li a {
            font-size: 13px;
        }
        .logo {
            zoom: 80%;
        }
        .footer_contact {
            margin-top: 5px;
            .red {
                a {
                    font-size: 19px;
                }
            }
            .phone {
              
            }
            .icons {
                margin-left: 15px;
                a {
                    margin-right: 15px;
                   
                }
            }
        }
    }
    @media screen and (max-width: 768px){
        padding: 29px 0;
        .footer-block nav {
            display: none;
        }
        .footer_contact {
            flex-wrap: wrap;
            justify-content: space-between;
            .phone {
                margin-left: 0;
                padding-left: 20px;
            }
        }
        .gmail-icon {
            display: block;
            flex-grow: 2;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        .footer_contact  {
            .red {
                a {
                    font-size: 18px;
                    line-height: 23px;
                }
                
            }
            .gmail-icon {
                width: 214px;
                flex-grow: 0;
                margin-top: 7px;
            }
            .icons {
                margin-left: 0;
                margin-top: 7px;
                
                a {
                    zoom: 75%;
                    &:last-child {
                        margin-right: 0;
                    }
                }
            }
          
        }
    }
    @media screen and (max-width: 640px){
        padding: 25px 0;
        .logo {
            zoom: 70%;
            margin-right: 30px;
        }
        .footer_contact .red a {
            font-size: 16px;
            line-height: 20px;
        }
        .footer_contact .gmail-icon {
            width: 190px;
           
        }
        .footer_contact .phone {
            padding-left: 0;
        }
    }
    @media screen and (max-width: 575px){
        padding: 20px 0;
        .logo {
            margin-right: 50px;
        }
        .footer_contact {
            justify-content: flex-start;
            .phone {
                order: 3;
                margin-top: 15px;
            }
            .gmail-icon {
                order: 2;
                width: auto;
                margin-top: 0;
                margin-left: 20px;
            }
            .gmail {
                order: 1;
            }
            .icons {
                order: 4;
                margin-left: 20px;
                margin-top: 15px;
            }
        }
        
    }
    @media screen and (max-width: 497px){
        padding: 20px 0;
        .logo {
            margin-right: 50px;
        }
        .footer_contact {
            justify-content: flex-start;
            .red {
                a {
                    font-size: 12px;
                }
                
            }
            
            .phone {
                order: 3;
                margin-top: 15px;
            }
            .gmail-icon {
                order: 2;
                width: auto;
                margin-top: 0;
                margin-left: 20px;
                zoom: 90%;
                a {
                    display: flex;
                    align-items: center;
                }
            }
            .gmail {
                order: 1;
            }
            .icons {
                order: 4;
                margin-left: 20px;
                margin-top: 15px;
                zoom: 90%;
                a {
                    display: flex;
                    align-items: center;
                }
            }
        }
    }
    @media screen and (max-width: 448px){
        .logo {
            zoom: 50%;
        }
        .footer_contact {
         
            .red {
                a {
                    font-size: 11.5px;
                    line-height: 11px;
                }
                display: flex;
                align-items: center;
            }
            
            
            .icons {
                order: 4;
                margin-left: 0px;
                margin-top: 15px;
                zoom: 90%;
                a {
                    margin-right: 0;
                    margin-left: 15px;
                    display: flex;
                    align-items: center;
                }
            }
        }
    }
    @media screen and (max-width: 340px){
        padding: 11px 0;
        .logo {
            zoom: 50%;
        }
        .footer_contact {
         
            .red {
                a {
                    font-size: 9px;
                    line-height: 11px;
                }
                display: flex;
                align-items: center;
            }
            .phone {
                margin-top: 10px;
            }
            .gmail-icon {
                a {
                    zoom: 60%;;
                }
            }
            .icons {
                margin-top: 5px;
                order: 4;
                margin-left: 0px;
               
                zoom: 90%;
                a {
                    zoom: 50%;
                    margin-right: 0;
                    margin-left: 15px;
                    display: flex;
                    align-items: center;
                }
            }
        }
    }
   
}