.navbar {
    position: absolute;
    width: 100%;
    top: 0;
    background: linear-gradient(84.16deg, rgba(255, 255, 255, 0.95) 0%, rgba(255, 255, 255, 0.7125) 100%);
    z-index: 99;
    
    .container {
        height: 104px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        
    }
    .menu {
        display: flex;
        li {
            list-style-type: none;
            margin-right: 30px;
            &:last-child {
                margin-right: 0;
            } 
            a {
                text-transform: uppercase;
                color: #000;
                font-family: 'Cera Pro Medium';
                font-size: 15px;
                line-height: 19px;
                transition: all 0.5s ease;
                span {
                    display: none;
                }
                &:hover {
                    color: #DA0D73;
                }
            }
        }
     
    }
    .hamburger {
        display: none;
    }
    .icons {
        
        a {
            margin-right: 15px;
            &:last-child {
                margin-right: 0;
            }
        }
        
    }

    @media screen and (max-width: 1300px) {
        .logo {
            zoom: 85%;
        }
        .menu {
            li {
                margin-right: 23px;
                &:last-child {
                    margin-right: 0;
                } 
                a {
                    font-size: 14px;
                    line-height: 18px;
                }
            }
        }
        .icons {
            a {
                zoom: 90%;
                margin-right: 10px;
                &:last-child {
                    margin-right: 0;
                }
            }
        }
    }
    @media screen and (max-width: 1100px) {
      
        .container {
            flex-wrap: wrap;
            height: 150px;
        }
        .logo {
            zoom: 100%;
            width: 50%;
            display: flex;
            justify-content: center;
        }
        nav {
            order: 1;
            margin: 0 auto;
        }
        .menu {
            margin-top: 0px;
            transform: translateY(-10px);
            li {
               margin-right: 30px;
                &:last-child {
                   
                } 
                a {
                   font-size: 15px;
                }
            }
        }
        .icons {
            width: 50%;
            order: 0;
            display: flex;
            justify-content: center;
            a {
                zoom: 110%;
                margin-right: 30px;
                &:last-child {
                
                }
            }
        }
    }

    @media screen and (max-width: 768px) {
        .container {
            height: 104px;
        }
        .logo {
            justify-content: flex-start;
            width: auto;
        }

        nav {
            position: absolute;
            display: none;
            width: 436px;
            height: 310px;
            z-index: 99;
            top: 0;
            right: 0;
            &.open {
                
            }
            .menu {
                transform: none;
                width: 100%;
                height: 100%;
                z-index: 99;
                flex-direction: column;
                
                padding: 6px 81px 37px 42px;

                background: linear-gradient(204.26deg, #FFFFFF 19.4%, rgba(255, 255, 255, 0.89) 100%);
                box-shadow: -10px 14px 44px rgba(0, 0, 0, 0.38);
                li {
                    margin-right: 0;
                    margin-top: 25px;
                    a {
                        font-size: 20px;
                        line-height: 25px;
                        span {
                            display: inline;
                        }
                    }
                }
            }
        }
        .hamburger {
            display: flex;
            flex-direction: column;
            transition: all 1s ease;
            span {
                width: 30px;
                height: 4px;
                background: #000;
                display: inline-block;
                margin-top: 3px;
                transition: all 1s ease;
                &:first-child {
                    margin-top: 0;
                }
            }
            &.close {
                position: absolute;
                top: 23px;
                right: 21px;
                z-index: 99;
                width: 27px;
                height: 27px;
                zoom: 110%;
                span {
                    margin-top: 0;
                    position: absolute;
                    &:nth-child(1) {
                        transform: translate( -2px, 11px) rotate(45deg);
                    }
                    &:nth-child(2) {
                        transform: translate( -2px,11px) rotate(-45deg);
                    }
                    &:nth-child(3) {
                        display: none;
                    }
                }
            }
        }

        .icons {
            width: auto;
            justify-content: flex-start;
          
            a {
                zoom: 100%;
                margin-right: 20px;
                &:last-child {
                
                }
            }
        }
    }
    @media screen and (max-width: 640px) {
        .container {
            height: 90px;
        }
        .logo {
            zoom: 90%;
            
        }
        nav .menu {
            padding: 25px 75px 30px 35px;
            li {
                &:first-child {
                    margin-top: 0;
                }
                a {
                    font-size: 18px;
                    line-height: 22px;
                }
            }
        }
        .icons {
            a {
                zoom: 90%;
            }
        }
    }
    @media screen and (max-width: 575px) {
        .logo {
            zoom: 85%;
        }
        nav {
            width: 100vw;
            .menu {
                padding-left: 50px;
                padding-right: 70px;
                li {
                    a {
                        font-size: 17px;
                        line-height: 21px;
                    }
                }
            }
        }
        .icons {
            a {
                zoom: 85%;
            }
        }
    }
    @media screen and (max-width: 440px) {
        .logo {
            zoom: 80%;
        }
        nav .menu {
           padding-bottom: 10px;
            li {
                margin-top: 20px;
                &:first-child {
                   
                }
                a {
                   font-size: 16px;
                }
            }
        }
        .icons {
            a {
                margin-right: 0;
                margin-left: 20px;
                zoom: 80%;
                &:first-child {
                    margin-left: 0;
                }
            }
            &-messenger {
                display: none;
            }
        }
    }
    @media screen and (max-width: 365px) {
        .container {
            height: 77px;
        }
        .logo {
            zoom: 74%;
        }
        nav .menu {
            height: auto;
            padding: 13px 68px 29px 27px;
             li {
                 margin-top: 19px;
                 &:first-child {
                    
                 }
                 
                 a {
                     max-height: 33px !important;
                     
                    font-size: 15px;
                    line-height: 15px;
                 }
             }
         }
        .hamburger.close {
            zoom: 76%;
            top: 22px;
            right: 23px;
        }
        .icons {
            a {
               zoom: 65%;
               transform: translateY(6px);
               margin-left: 25px;
                &:first-child {
                    margin-left: 0;
                }
            }
            &-messenger {
             
            }
        }
    }
}