.section-5 {
    padding-top: 93px;
    background: linear-gradient(0deg, #FCF0FB 16.09%, #FCF7F0 82.53%);
    .container {
        transform: none;
        background: #fff;
        padding: 38px;
        .title{
            h2 {
                padding-right: 15%;
            }
        }
        
    }
    
    .questions {
        margin-top: 60px;
        
        &-item {
            height: 115px;
            position: relative;
            display: flex;
            align-items: center;
            margin-top: -20px;
            cursor: pointer;
            p {
                z-index: 1;
                font-size: 24px;
                line-height: 30px;
                font-family: 'Cera Pro Medium';
                color: #FFFFFF;
                padding-left: 58px;
                padding-right: 30%;
                margin-top: -20px;
            }
            img {
                width: 100%;
                position: absolute;
                z-index: 0;
            
                box-shadow: 0px 0px 5px rgba(0,0,0, 0.3);
                border-radius: 18px 18px 0px 0px;
                height: 100%;
            }
            .plus {
                position: absolute;
                z-index: 9;
                top: 34.5px;
                right: 6%;
                width: 26px;
                height: 26px;

                span {
                    display: block;
                    width: 5px;
                    height: 26px;
                    background: #646464;
                    position: absolute;
                    transform: translate(11px, 0px);
                    &:first-child {
                        transform: translate(11px, 0px) rotate(90deg);
                    }
                    &:last-child {
                        // transform: rotate(90deg);
                    }
                }
            }
            &:last-child{
                p {
                    margin-top: 0;
                }
                .plus {
                    top: calc(50% - 13px);
                }
            }
        }
        
    }
    // .questions-close {
    //     height: 100vh;
    //     width: 100vw;
    // }


    .section-5-top {
        padding-bottom: 44px;
    }
    .questions-item .container {
        display: none;
    }
    
    .questions-item.questions-item-active {
        position: fixed;
        height: 100vh;
        width: 100vw;
        top: 0;
        left: 0;
        background: rgba(0,0,0, 0.8);
        z-index: 9999;
        margin-top: 0;
        
        p {
            color: #000;
            margin-top: 0;
        }

        .container {
            padding: 0;
            margin: 0 auto;
            border-radius: 20px;
            display: block;
            .title {
                background: #7A03B2;
                height: 129px;
                display: flex;
                align-items: center;
                border-radius: 20px 20px 0 0;
                position: relative;
                h2 {
                    
                    color: #fff;
                    padding-left: 47px;
                }
                .questions-item-active_plus {
                    position: absolute;
                    z-index: 9;
                    top: calc(50% - 18);
                    right: 6%;
                    width: 36px;
                    height: 36px;
    
                    span {
                        display: block;
                        width: 6px;
                        height: 36px;
                        background: #fff;
                        position: absolute;
                        
                        &:first-child {
                            transform: translate(11px, 0px) rotate(45deg);
                        }
                        &:last-child {
                            transform: translate(11px, 0px) rotate(-45deg);
                        }
                    }
                }
            }
            
            .questions-item__pop-text {
                padding-left: 44px;
                padding-right: 120px;
                padding-top: 30px;
                padding-bottom: 45px;
                p {
                    font-size: 28px;
                    line-height: 35px;
                    font-family: 'Cera Pro Medium';
                    color: #000;
                    padding: 0;
                    .blue {
                        color: #3139FF;
                    }
                    &:nth-child(2) {
                        margin-top: 25px;
                        font-family: 'Cera Pro Bold';
                        
                    }
                }
                
            }
            .section-5-bottom {
                padding-top: 22px;
                padding-bottom: 35px;
                p {
                    padding-left: 44px;
                    padding-right: 30px;
                    font-size: 20px;
                    line-height: 25px;
                    font-family: 'Cera Pro Regular';
                    .purple {
                        font-family: 'Cera Pro Medium';
                    }
                    &:nth-child(1) {
                        padding-right: 215px;
                    }
                    &:nth-child(2) {
                        margin-top: 8px;
                    }
                }
                .medium {
                    font-family: 'Cera Pro Medium';
                }
            }
        }
        .close {
            display: none;
        }
        h2 {
            width: 100%;
        }


        @media screen and (max-width: 1300px) {
            .container .questions-item__pop-text {
                padding-right: 30px;
            }
        }
        @media screen and (max-width: 991px) {
            .container .section-5-bottom p {
                font-size: 18px;
                line-height: 23px;
            }
            .container .questions-item__pop-text p {
                font-size: 25px;
            }
            .container .section-5-bottom p:nth-child(1) {
                padding-right: 50px;
            }
        }
        @media screen and (max-width: 768px) {
            .container {
                width: 90vw;
            }
            .container .questions-item__pop-text {
                padding-left: 40px;
            }
            .container .title h2 {
                padding-left: 42px;
            }
        }
        @media screen and (max-width: 768px) {
           
            .container .section-5-bottom p {
                font-size: 17px;
                line-height: 21px;
            }
            .questions-item-active_plus {
                zoom: 80%;
            }
            .container .questions-item__pop-text {
                padding-top: 21px;
            }
            .container .questions-item__pop-text p:last-child {
                margin-top: 24px;
                margin-bottom: 21px;
            }
            .container .questions-item__pop-text p {
                font-size: 21px;
                line-height: 26px;
            }
            .container .section-5-bottom p:nth-child(1) {
                padding-right: 50px;
            }

            .container .section-5-bottom p {
                padding-left: 38px;
                
            }
            .container .section-5-bottom  {
                .medium {
                    font-size: 15px;
                    font-family: 'Cera Pro Bold';
                }
            }
        }

        @media screen and (max-width: 575px) {
            .container .title {
                height: 80px;
                h2 {
                    margin-bottom: 0;
                }
                
                .questions-item-active_plus {
                    zoom: 70%;
                }
            }
            .container .title h2 {
                padding-left: 12px;
            }
            .container .questions-item__pop-text{
                padding-left: 12px;
                padding-right: 12px;
            }
            .container .questions-item__pop-text p {
                font-size: 13px;
                line-height: 16px;
                font-family: 'Cera Pro Bold';
                
            }
            .container .questions-item__pop-text p:last-child {
                font-family: 'Cera Pro Black';
                margin-top: 5px;
                margin-bottom: 15px;
            }
            
            .container .section-5-bottom {
                padding-top: 10px;
                padding-bottom: 19px;
                p {
                    padding: 0 12px;
                    font-size: 14px;
                    line-height: 18px;
                    &.medium {
                        line-height: 21px;
                        font-size: 12px;
                    }
                }
            }
        }
        @media screen and (max-width: 375px) {
            .container .title {
                height: 56px;
                .questions-item-active_plus {
                    zoom: 50%;
                }
            }
            .container {
                width: 95vw;
            }
            .container .section-5-bottom p.medium {
                padding-right: 4px;
            }
        }
    }
    .questions-item.questions-item-active {
        @media screen and (max-height: 650px) and (orientation: landscape) {
            .container .title {
                height: 70px;
                h2 {
                    font-size: 22px;
                    padding-left: 20px;
                    line-height: 23px;
                }
                .questions-item-active_plus {
                    zoom: 80%;
                    right: 2%;
                }
            }
            .container .section-5-bottom p {
                font-size: 16px;
                line-height: 18px;
            }
            
            .container .questions-item__pop-text {
                padding: 10px 20px;
            
            }
            .container .questions-item__pop-text p:last-child {
                margin-top: 15px;
                margin-bottom: 5px;
                font-size: 16px;
            }
            .container .questions-item__pop-text p {
                font-size: 16px;
                line-height: 26px;
                line-height: 20px;
            }
            .container .section-5-bottom p:nth-child(1) {
                padding-right: 50px;
            }
    
            .container .section-5-bottom p {
                padding-left: 20px;
                padding-right: 20px;

            }
            .container .section-5-bottom  {
                padding: 20px 0;
                .medium {
                    font-size: 15px;
                    font-family: 'Cera Pro Bold';
                }
            }
        }


        @media screen and (max-height: 390px) and (orientation: landscape) {
            .container .title {
                h2 {
                    font-size: 15px;
                    padding-right: 0;
                    height: 100%;
                    display: flex;
                    align-items: center;
                }
                .questions-item-active_plus {
                    zoom: 85%;
                }
            }
            .container .section-5-bottom p {
                font-size: 13px;
                line-height: 13px;
                
            }
            
            .container .questions-item__pop-text {
                padding: 10px 20px 20px 20px;

            
            }
            .container .questions-item__pop-text p:last-child {
                margin: 15px 0;
                font-size: 13px;
                line-height: 13px;
                margin: 0;
            }
            .container .questions-item__pop-text p {
                margin: 0;
                margin-bottom: 10px;
                font-size: 13px;
                line-height: 13px;
            }
            .container .section-5-bottom p:nth-child(1) {
                padding-right: 50px;
            }
    
            .container .section-5-bottom p {
                padding-left: 20px;
                padding-right: 20px;
            }
            .container .section-5-bottom  {
                .medium {
                    font-size: 15px;
                    font-family: 'Cera Pro Bold';
                }
            }

            .container .title {
                height: 56px;
                .questions-item-active_plus {
                    zoom: 50%;
                }
            }
            .container {
                width: 93vw;
            }
            .container .section-5-bottom p.medium {
                padding-right: 4px;
            }
        }
        
    }
    








    @media screen and (max-width: 768px) {
        .container {
            padding-top: 20px;
        }
        padding-top: 23px;
        .questions {
            &-item {
                p {
                    font-size: 20px;
                    line-height: 25px;
                    padding-left: 26px;
                }
                .plus {
                    right: 5%;
                }
            }
        }
        .section-5-top {
            padding-bottom: 59px;
        }
        .section-5-bottom {
            padding: 17px 20px 24px 15px;
            
        }
        
        .section-3-bottom.container p .red {
            right: -6px;
        }
        .questions-item.questions-item-active .container .questions-item__pop-text {
            padding-bottom: 10px;
        }
    }
    @media screen and (max-width: 640px) {
        .questions {
            &-item {
                height: 100px;
                p {
                    font-size: 17px;
                    line-height: 20px;
                    padding-left: 15px;
                    padding-right: 23%;
                }
                .plus {
                    right: 5%;
                    top: 40.5px;
                    zoom: 80%;
                }
            }
        }
        
    }
    @media screen and (max-width: 575px) {
        .container {
            padding: 20px;
        }
        padding-top: 100px;
        .container .title h2 {
            font-size: 15px;
        }
        .questions {
            &-item {
                height: 80px;
                p {
                    font-size: 15px;
                    line-height: 17px;
                    padding-left: 12px;
                }
                .plus {
                    top: 27.5px;
                }
            }
        }
        .section-5-top {
            padding-bottom: 48px;
        }
    }
    @media screen and (max-width: 460px) {
        
        .questions {
            &-item {
                height: 80px;
                p {
                    font-size: 13px;
                    line-height: 16px;
                    padding-left: 10px;
                }
                .plus {
                    top: 27.5px;
                }
            }
        }
        
    }
    @media screen and (max-width: 375px) {
        .container .title h2 {
            font-size: 13px;
        }
        .questions {
            &-item {
                height: 70px;
                p {
                    font-size: 12px;
                    line-height: 15px;
                    padding-left: 9px;
                    padding-right: 17%;
                }
                .plus {
                    top: 42px;
                    right: 4%;
                    zoom: 50%;
                }
            }
        }
        .section-5-bottom {
            padding: 10px 10px 19px 10px;
        }
    }
    
}